

.selected-conv {
    background: #0d6efd;
    color: white;
}

.card-header-layout {
    display: flex;
    align-content: center;
    justify-content: space-between;
}

.inner-header {
    display: flex;
    align-content: center;
    grid-gap: 10px;
    gap: 10px;
}

.close-style {
    background: transparent;
    border: none;
    color: red;
    transition: color 200ms ease-in-out;
}


.close-style:hover {
    color: darkred;
}
.inner-chat-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: space-between;
}

.chat-header {
    width: 100%;
    padding-bottom: 10px;
    border-bottom: black 1px solid;
}

.message-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
}

.message-left {
    width: 100%;
    display: flex;
    justify-content: left;
}


.message-right {
    width: 100%;
    display: flex;
    justify-content: right;
}

.message-bar {
    margin-top: 10px;
    display: flex;
    grid-gap: 10px;
    gap: 10px;
    justify-content: space-between;
}

.input-message {
    border: black 1px solid;
    width: 100%;
    border-radius: 15px;
}

.input-message:focus-visible {
    outline: none!important;
    border-color: black;

}



.send-message-button {
    fill: white;
    aspect-ratio: 1/1;
    width: 45px;
    border-radius: 50%;
    border: #0d6efd 2px solid;
    background: #0d6efd;
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    transition: background-color 200ms ease-in;
}

.send-message-button:hover {
    background: rgba(13, 110, 253, 0.79);
}

.send-icon {
    height: 35px;
    width: 35px;
}

.inner-message-container {
    border-radius: 10px;
    padding: 5px;
    max-width: 400px;
    width: 40%;
    min-width: 80px;
    position: relative;
    margin-right: 15px;
    border: #2a2a2a 1px solid;
}

.message-content {
    text-align: left;
}

.time-stamp-text {
    color: #b7b7b7;
    position: absolute;
    right: 5px;
    bottom: 4px;
}

.message-header {
    width: 100%;
    color: #0d6efd;
}

.blue {
    background-color: #0d6efd;
    color: white;
}

.blue-header {
    color: white;
    font-weight: bold;
}
/* Set the overall layout */
.main-container {
    display: flex;
    justify-content: flex-start;
    align-items: stretch;
    margin: 0;
    padding: 0;
    height: calc(100vh - 56px);
    font-family: Arial, sans-serif;
}

.conv-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    grid-gap: 10px;
    gap: 10px;
}

/* Style for the left vertical bar */
.sidebar {
    width: 30%;
    background-color: #f1f1f1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-content: space-between;
    padding: 10px;
}

/* Style for the chat container */
.chat-container {
    flex-grow: 1;
    padding: 10px;
    background-color: #ffffff;
}

/* Style for individual conversation items */
.conversation-item {
    margin-bottom: 10px;
    padding: 10px;
    background-color: #e9e9e9;
    border-radius: 5px;
}

/* Style for conversation item text */
.conversation-item-text {
    font-weight: bold;
}

/* Style for active conversation item */
.conversation-item.active {
    background-color: #d1d1d1;
}

/* Style for chat input area */
.chat-input {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    resize: none;
}

.chat-input:focus-visible {
    outline: none!important;
    border-color: #c9b532;
    box-shadow: 0 0 10px #cb205d;
}

.chat-input:focus-visible {
    outline: none!important;
    border-color: #71ce8e;
    box-shadow: 0 0 10px #ad7171;
}

/* Style for send button */
.send-button {
    display: block;
    width: 100%;
    margin-top: 10px;
    padding: 10px;
    background-color: #4caf50;
    color: #ffffff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

/* Style for send button on hover */
.send-button:hover {
    background-color: #45a049;
}

.close-button {
    background: transparent;
    border: none;
}

.auto-suggestion {
    padding-left: 5px;

}

.auto-suggestion:hover :focus {
    background-color: #0d6efd;
    color: white;
}

.suggestion-list {
    padding-left: 0;
}


